:root {
  --font-mono: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", monospace;
  --font-rubik-mono: "Rubik Mono One", monospace;
  --font-rubik: 'Rubik', sans-serif;
  --font-icons: "Font Awesome 6 Free";
  --animation-time: 5s;
  --github-grey: #161b22;
  --obsidian-black: #0B1215;
  --deep-navy-blue: #011222;
  --rich-black: #020d19;
  --off-white: #fbfbfb;
  --ghost-white: #f8f8ff;
  --alice-blue: #f0f8ff;
  --branding-blue: #4ab3fd;
}

html {
  scroll-padding-top: 3rem;
}

body {
  background: var(--github-grey) !important;
  color: #fff !important;
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--font-rubik-mono), monospace;
}

h1 {
  font-size: 2.5rem !important;
}

h2 {
  font-size: 2.5rem !important;
}

h3 {
  font-size: 1.8rem !important;
}

h4 {
  font-size: 1.2rem !important;
}

p {
  font-family: var(--font-rubik);
}

img {
  width: 100%;
  height: auto;
}

.pos-abs {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  overflow: hidden;
}

.in-ratio {
  position: relative;
  display: block;
  width: 100%;
  height: 0;
  margin: 0 auto;
  padding-bottom: 35%;
  overflow: hidden;
}

.font-mono {
  font-family: var(--font-rubik-mono);
}

.font-rubik {
  font-family: var(--font-rubik);
}

.rounded-image {
  border-radius: 100%;
}

.header {
  background: #161b22;
}

.navbar-dark .navbar-toggler {
  border: none;
}

.navbar-toggler:focus {
  box-shadow: none;
}

.company-logo {
  max-width: 100px;
}

.main-content {
  margin-top: 5rem;
}

.section-padding {
  padding-top: 2rem !important;
  padding-bottom: 2rem !important;
}

.external-link {
  color: #000;
  text-decoration: none;
}

.external-link:hover {
  color: #444;
}

.card {
  transition: ease-in-out 0.2s;
  background-color: #21262d !important;
  border: 1px solid #30363d !important;
  padding: 0;
}

.card-text {
  color: #fff;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px,
    rgba(0, 0, 0, 0.05) 0px 5px 10px;
}

.dark-card {
  position: relative;
  min-width: 0;
  word-wrap: break-word;
  background-color: #21262d;
  background-clip: border-box;
  border: 1px solid #30363d;
  border-radius: 6px;
  padding: 1rem 1rem;
  transition: ease-in-out 0.2s;
}

.dark-card:hover {
  transform: translateY(-5px);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px,
    rgba(0, 0, 0, 0.05) 0px 5px 10px;
}

.project-img {
  border-radius: 0.25rem 0.25rem 0 0;
}

.highlighted {
  color: var(--branding-blue);
}

.text-muted {
  color: #6c757d !important;
}

/* Glitch Effects */
.glitch {
  text-shadow: 0.06rem 0 0.06rem #ea36af, -0.125rem 0 0.06rem #75fa69;
  animation-duration: 0.1s;
  animation-name: textflicker;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.glitch-hover:hover {
  text-shadow: 0.06rem 0 0.06rem #ea36af, -0.125rem 0 0.06rem #75fa69;
  animation-duration: 0.1s;
  animation-name: textflicker;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

@keyframes textflicker {
  from {
    text-shadow: 1px 0 0 #ea36af, -2px 0 0 #75fa69;
  }
  to {
    text-shadow: 2px 0.5px 2px #ea36af, -1px -0.5px 2px #75fa69;
  }
}
